@import "../../styles/shared.scss";


.main {
  color: #000000;
  margin-left: auto;
  margin-right: auto;
  width: $mobile;





  @media screen and (min-width: $tablet) {
    max-width: $tablet;


  }

  @media screen and (min-width: $desktop) {
    max-width: $desktop;
  }

  @media screen and (min-width: $desktopL) {
    max-width: $desktopL;
  }

  @media screen and (min-width: $desktop4k) {
    max-width: $desktop4k;
  }

}